import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { graphql } from 'gatsby';

import Banner from '../components/banner';
import Layout from '../components/layout';
import SEO from '../components/seo';
import PostCard from '../components/postCard';
import SideBar from '../components/SideBar';
import '../utils/css/index.scss';
import '../utils/css/global.scss';

const Categories = ({ pageContext, data, location }) => {
    const siteTitle = data.site.siteMetadata.title;
    const posts = data.allMarkdownRemark.edges;

    const { social, author } = data.site.siteMetadata;
    const facebook = data.site.siteMetadata.social.facebook;
    let postCounter = 0;
    const { category } = pageContext;

    return (
        <Layout
            location={location}
            title={siteTitle}
            facebook={facebook}
            social={social}
            author={author}
        >
            <SEO title={category} />
            {/* <Bio /> */}
            <Banner imgFluid={data.file.childImageSharp.fluid} />
            <div className="post-tags">Category : {category}</div>
            <div className="index-container">
                <SideBar />
                <div className="post-feed">
                    {posts.map(({ node }, index) => {
                        postCounter++;
                        return (
                            <PostCard
                                key={node.fields.slug}
                                count={postCounter}
                                node={node}
                                postClass={`post`}
                            />
                        );
                    })}
                </div>
            </div>
        </Layout>
    );
};

Categories.propTypes = {
    location: PropTypes.object,
    pageContext: PropTypes.shape({
        tag: PropTypes.string.isRequired
    }),
    data: PropTypes.shape({
        allMarkdownRemark: PropTypes.shape({
            totalCount: PropTypes.number.isRequired,
            edges: PropTypes.arrayOf(
                PropTypes.shape({
                    node: PropTypes.shape({
                        frontmatter: PropTypes.shape({
                            title: PropTypes.string.isRequired
                        }),
                        fields: PropTypes.shape({
                            slug: PropTypes.string.isRequired
                        })
                    })
                }).isRequired
            )
        })
    })
};

export default Categories;

export const pageQuery = graphql`
    query($category: String) {
        site {
            siteMetadata {
                title
                author
                social {
                    facebook
                }
            }
        }
        file(relativePath: { eq: "banner.png" }) {
            id
            childImageSharp {
                fluid(maxWidth: 295) {
                    ...GatsbyImageSharpFluid_noBase64
                }
            }
        }
        allMarkdownRemark(
            limit: 2000
            sort: { fields: [frontmatter___date], order: DESC }
            filter: { frontmatter: { categories: { in: [$category] } } }
        ) {
            totalCount
            edges {
                node {
                    excerpt
                    fields {
                        slug
                    }
                    frontmatter {
                        date(formatString: "MMMM DD, YYYY")
                        title
                        description
                        categories
                        thumbnail {
                            childImageSharp {
                                fluid(maxWidth: 450) {
                                    ...GatsbyImageSharpFluid
                                }
                            }
                        }
                    }
                    timeToRead
                }
            }
        }
        imageSharp(fluid: { src: { regex: "/ogImage.jpg/" } }) {
            fluid {
                src
            }
        }
    }
`;
